import {Record} from 'immutable';

export const Station = Record({
  name: "",
  latitude: null,
  longitude: null,
  distance: "",
  timeUntilRide: null,
  bikesAvailable: null,
  totalSpots: null,
}, 'Station');


export function makeStationFromRaw(attributes) {
  return new Station({
    name: attributes.stationName,
    latitude: attributes.latitude,
    longitude: attributes.longitude,
    distance: attributes.distance,
    timeUntilRide: attributes.timeUntilRide,
    bikesAvailable: attributes.bikesAvailable,
    totalSpots: attributes.totalSpots,
  });
}


