import React from 'react';
import {Route} from 'react-router-dom';
import StationsLocator from '../components/StationsLocator.jsx';

const App = () => (
  <div>
    <main>
      <Route exact path="/" component={StationsLocator}/>
    </main>
  </div>
)

export default App



