import React from 'react';
import {css, StyleSheet} from 'aphrodite';
import P from 'prop-types';

import {Station} from "../models/station";


function StationCardRow({title, content}) {
  return (
    <div className={css(styles.stationCardRowSpacer)}>
        <span className={css(styles.stationCardText, styles.stationCardHeader)}>
          {title}
        </span>
      <span className={css(styles.stationCardText)}>
          {content}
        </span>
    </div>
  );
}

StationCardRow.propTypes = {
  title: P.string.isRequired,
  content: P.node.isRequired,
}

export default function StationCard({station}) {
  return (
    <div className={css(styles.stationCard)}>
      <div className={css(styles.stationCardText)}>{station.name}</div>
      <StationCardRow
        title={"Bikes Available"}
        content={station.bikesAvailable}
      />
      <StationCardRow
        title={"Total Spots"}
        content={station.totalSpots}
      />
      <StationCardRow
        title={"Time until Ride"}
        content={`${Math.round(station.timeUntilRide * 100) / 100} min`}
      />
    </div>
  );
}
StationCard.propTypes = {
  station: P.instanceOf(Station).isRequired,
}


const styles = StyleSheet.create({
  stationCard: {
    display: 'flex',
    flexFlow: 'column nowrap',
  },
  stationCardText: {
    fontFamily: '"Helvetica Neue", Helvetica, Arial, sans-serif',
    fontSize: 12,
    fontWeight: 400,
  },
  stationCardHeader: {
    fontWeight: 700,
    marginRight: 5,
  },
  stationCardRowSpacer: {
    marginTop: 2,
  }
});




