import {createSelector} from 'reselect';

const substateSelector = state => state.stationsData;

export const stationsDataSelector = createSelector(
  substateSelector, (substate) => substate.stations
);

export const stationsDataErrorSelector = createSelector(
  substateSelector, (substate) => substate.error
);

export const stationsDataInitializedSelector = createSelector(
  substateSelector, (substate) => substate.initialized
);

export const fetchingStationsDataSelector = createSelector(
  substateSelector, (substate) => substate.fetching
);




