import React, {Component} from 'react';
import {css, StyleSheet} from 'aphrodite';
import P from 'prop-types';
import IP from 'react-immutable-proptypes';
import {connect} from 'react-redux';
import {geolocated, geoPropTypes} from 'react-geolocated';

import {
  stationsDataErrorSelector,
  stationsDataInitializedSelector,
  stationsDataSelector
} from "../redux/selectors/stations_selector";
import {fetchStationsForLocation} from "../redux/actions/stations_actions";
import {Station} from "../models/station";

import MapContainer from './MapContainer.jsx';
import Throbber from './Throbber.jsx';
import StationList from "./StationList";
import ErrorMessage from "./ErrorMessage";

class StationsLocator extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hasFiredRequest: false,
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.coords && !this.state.hasFiredRequest) {
      const {coords, onFetchStationsForLocation} = nextProps;
      onFetchStationsForLocation(coords.latitude, coords.longitude);
      this.setState({hasFiredRequest: true});
    }
  }

  render() {
    const {stationsDataInitialized, stationData, stationDataError, coords} = this.props;

    if (!coords) {
      return (
        <div className={css(styles.center)}>
          <Throbber/>
        </div>
      );
    }

    let contentSection;
    if (!stationsDataInitialized) {
      contentSection = (<ErrorMessage hideHeader message={"Loading Stations..."}/>);
    } else if (stationDataError) {
      contentSection = (<ErrorMessage message={stationDataError}/>);
    } else if (stationData.size === 0) {
      contentSection = (<ErrorMessage hideHeader message={"Could not find any nearby stations."}/>);
    } else {
      contentSection = (<StationList stationData={stationData}/>);
    }

    const {latitude, longitude} = coords;
    return (
      <div className={css(styles.wrapper)}>
        <div className={css(styles.content)}>
          <div className={css(styles.mapDisplay)}>
            <MapContainer
              latitude={latitude}
              longitude={longitude}
              stationData={stationData}
            />
          </div>
          {contentSection}
        </div>
      </div>
    );
  }
}

StationsLocator.propTypes = {
  stationData: IP.listOf(P.instanceOf(Station)),
  stationDataError: P.string,
  stationsDataInitialized: P.bool,
  ...geoPropTypes
};

StationsLocator.defaultProps = {
  stationData: null,
  stationDataError: null,
  stationsDataInitialized: false,
};

const mapStateToProps = (state) => {
  return {
    stationData: stationsDataSelector(state),
    stationDataError: stationsDataErrorSelector(state),
    stationsDataInitialized: stationsDataInitializedSelector(state),
  };
};

const mapDispatchToProps = (dispatch) => ({
  onFetchStationsForLocation: (latitude, longitude, isLookingForBike) => {
    dispatch(fetchStationsForLocation(latitude, longitude, isLookingForBike));
  }
});

export default geolocated({
  userDecisionTimeout: 5000,
})(
  connect(mapStateToProps, mapDispatchToProps)(StationsLocator)
);


const styles = StyleSheet.create({
  center: {
    position: 'absolute',
    top: 0,
    left: 0,
    display: 'flex',
    width: '100%',
    height: '100%',
    alignItems: 'center',
    justifyContent: 'center',
  },
  wrapper: {
    display: 'flex',
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'darkgray',
    justifyContent: 'center',
  },
  content: {
    position: 'relative',
    display: 'flex',
    flexFlow: 'column nowrap',
    alignItems: 'center',
    padding: 20,
  },
  mapDisplay: {
    position: 'relative',
    width: 1000,
    height: 750,
    minHeight: 300,
  },
});



