import {Record} from 'immutable';
import {actionTypes} from '../actions/selected_station_actions.js';

export const InitialState = Record({
  selectedStation: null,
});

function stationSelected(state, payload) {
  const {selectedStation} = payload;
  return state.merge({
    selectedStation,
  });
}

export default function stationsData(state = new InitialState(), action) {
  const handlers = {
    [actionTypes.STATION_SELECTED]: stationSelected,
  };

  const handler = handlers[action.type];
  return handler ? handler(state, action.payload) : state;
}



