import $ from 'jquery';

function getStationByCoordinates(latitude, longitude, isLookingForBike) {
  // return new Promise((resolve) => {
  //   setTimeout(() => {
  //     resolve(dummyData);
  //   }, 5000);
  // });
  const endpointUrl = `https://whispering-eyrie-24902.herokuapp.com/?lat=${latitude}&lon=${longitude}`;
  return new Promise(function (resolve, reject) {
    $.getJSON(endpointUrl).done((data) => {
      resolve(data);
    }).fail((error) => {
      reject(error);
    });
  });
}

export default {
  getStationByCoordinates,
};


