import React from 'react';
import {css, StyleSheet} from 'aphrodite';
import P from 'prop-types';

export default function ErrorMessage({message, hideHeader}) {
  return (
    <div className={css(styles.error)}>
      {
        !hideHeader &&
        <span className={css(styles.errorMessage)}>
          Unable to Load Stations
        </span>
      }
      <span className={css(styles.errorMessage)}>
       {message}
     </span>
    </div>
  );
}

ErrorMessage.propTypes = {
  message: P.string,
  hideHeader: P.bool,
}

const styles = StyleSheet.create({
  error: {
    position: 'relative',
    display: 'flex',
    flexFlow: 'column nowrap',
    width: '100%',
    height: 500,
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: 10,
    backgroundColor: '#0098e4',
    marginTop: 20,
    padding: 5,
  },
  errorMessage: {
    marginTop: 5,
    color: 'white',
    fontSize: 'x-large',
    wordWrap: 'break-word',
  },
});



