import {List, Record} from 'immutable';
import {actionTypes as stationsActionTypes} from '../actions/stations_actions.js';

export const InitialState = Record({
  stations: List(),
  error: null,
  fetching: false,
  initialized: false,
});


function fetch(state, payload) {
  const {isFetching} = payload;
  return state.merge({
    fetching: isFetching,
  });
}

function success(state, payload) {
  const {stationData} = payload;

  return state.merge({
    stations: stationData,
    error: null,
    fetching: false,
    initialized: true,
  });
}

function failure(state, payload) {
  const {error} = payload;

  return state.merge({
    stations: List(),
    error,
    fetching: false,
    initialized: true,
  });
}

export default function stationsData(state = new InitialState(), action) {
  const handlers = {
    [stationsActionTypes.FETCH_STATIONS]: fetch,
    [stationsActionTypes.RECEIVED_STATIONS_SUCCESS]: success,
    [stationsActionTypes.RECEIVED_STATIONS_FAILURE]: failure,
  };

  const handler = handlers[action.type];
  return handler ? handler(state, action.payload) : state;
}



