import React, {Component} from 'react';
import {css, StyleSheet} from 'aphrodite';
import P from 'prop-types';
import IP from 'react-immutable-proptypes';
import {GoogleApiWrapper, InfoWindow, Map, Marker} from 'google-maps-react';

import Throbber from './Throbber.jsx';
import {Station} from "../models/station";
import StationCard from './StationCard.jsx';
import {selectedStationSelector} from "../redux/selectors/selected_stations_selector";
import {connect} from "react-redux";
import {stationSelected} from "../redux/actions/selected_station_actions";

const citiBikeLogo = "https://4real-uploads-jellolabs-com.imgix.net/----web-ui/citibikeLogo.png?w=125";
const citiBikeDisabledLogo = "https://4real-uploads-jellolabs-com.imgix.net/----web-ui/citibikeLogoBW.png?w=125";


export class MapContainer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {},
    }

    // binding this to event-handler functions
    this._onMarkerClick = this._onMarkerClick.bind(this);
    this._onMapClicked = this._onMapClicked.bind(this);
  }

  _onMarkerClick(props, marker) {
    const {onStationSelected} = this.props;
    if (props.station) {
      onStationSelected(props.station);
    }

    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
  }

  _onMapClicked() {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  }

  render() {
    const {latitude, longitude, stationData, google} = this.props;
    const {activeMarker, showingInfoWindow, selectedPlace} = this.state;

    if (!latitude || !longitude) {
      return <Throbber/>;
    }

    return (
      <Map google={google}
           onClick={this._onMapClicked}
           zoom={16}
           initialCenter={{
             lat: latitude,
             lng: longitude,
           }}
      >
        <Marker onClick={this._onMarkerClick}
                name={'Current location'}/>

        {
          stationData.map((station) => (
            <Marker
              name={station.name}
              station={station}
              position={{
                lat: station.latitude,
                lng: station.longitude
              }}
              icon={station.bikesAvailable > 0 ? citiBikeLogo : citiBikeDisabledLogo}
              onClick={this._onMarkerClick}
            />
          ))
        }

        <InfoWindow
          marker={activeMarker}
          visible={showingInfoWindow}
        >
          {
            selectedPlace && selectedPlace.station ?
              <StationCard station={selectedPlace.station}/>
              :
              <div className={css(styles.center)}>
                <span className={css(styles.stationCardText)}>{selectedPlace.name}</span>
              </div>
          }
        </InfoWindow>
      </Map>
    )
  }
}

MapContainer.propTypes = {
  latitude: P.number,
  longitude: P.number,
  stationData: IP.listOf(P.instanceOf(Station)),
  selectedStation: P.instanceOf(Station),
  onStationSelected: P.func.isRequired,
};

MapContainer.defaultProps = {
  latitude: null,
  longitude: null,
  selectedStation: null,
}

const mapStateToProps = (state) => {
  return {
    selectedStation: selectedStationSelector(state),
  };
};

const mapDispatchToProps = (dispatch) => ({
  onStationSelected: (selectedStation) => {
    dispatch(stationSelected(selectedStation));
  }
});

const styles = StyleSheet.create({
  stationCardText: {
    fontFamily: '"Helvetica Neue", Helvetica, Arial, sans-serif',
    fontSize: 18,
    fontWeight: 700,
  },
  center: {
    display: 'flex',
    width: '100%',
    height: '100%',
    alignItems: 'center',
    justifyContent: 'center',
    paddingTop: 10,
    paddingBottom: 10,
  },
});

export default GoogleApiWrapper({
  apiKey: 'AIzaSyC2tU7VqsHnFNo-oDgsnGXBdOvRA52m7As',
  version: '3',
})(connect(mapStateToProps, mapDispatchToProps)(MapContainer));



