import React from 'react';
import PropTypes from 'prop-types';
import {css, StyleSheet} from 'aphrodite';

export default class Throbber extends React.Component {
  render() {
    const {size, color} = this.props;
    const combinedStyleIndex = [size, color].join('_');
    return (
      <div className={css(
        styles.loading, styles[size], styles[combinedStyleIndex])}/>
    );
  }
}

Throbber.propTypes = {
  size: PropTypes.oneOf(['small', 'large']),
  color: PropTypes.oneOf(['gray', 'blue', 'holiday_gray', 'holiday_white']),
};

Throbber.defaultProps = {
  size: 'large',
  color: 'blue',
};

const styles = StyleSheet.create({
  loading: {
    marginLeft: 'auto',
    marginRight: 'auto',
    backgroundColor: 'transparent',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundSize: 'contain',
  },
  small: {
    minHeight: 30,
    width: 30,
  },
  large: {
    minHeight: 50,
    width: 50,
  },
  small_holiday_gray: {
    backgroundImage: 'url(//static.shopspring.com/web/img/Loader-Grey%401x.gif)',
  },
  small_holiday_white: {
    backgroundImage: 'url(//static.shopspring.com/web/img/Loader-White%401x.gif)',
  },
  large_holiday_gray: {
    backgroundImage: 'url(//static.shopspring.com/web/img/Loader-Grey%402x.gif)',
  },
  large_holiday_white: {
    backgroundImage: 'url(//static.shopspring.com/web/img/Loader-White%402x.gif)',
  },
  small_gray: {
    backgroundImage: 'url(//static.shopspring.com/web/img/bizarro-loader-30x30.gif)',
  },
  // NOTE:(jeff)
  // turns out we don't actually have a small, blue bizarro loader...
  // nothing right now tries to use it, but since its a valid combo, i don't want to just
  // delete this, so i'll just make it use small gray
  small_blue: {
    backgroundImage: 'url(//static.shopspring.com/web/img/bizarro-loader-30x30.gif)',
  },
  large_gray: {
    backgroundImage: 'url(//static.shopspring.com/web/img/bizarro-loader-50x50.gif)',
  },
  large_blue: {
    backgroundImage: 'url(//static.shopspring.com/web/img/bizarro-loader-50x50-blue.gif)',
  },
});



