import React, {Component} from 'react';
import {css, StyleSheet} from 'aphrodite';
import P from 'prop-types';
import IP from 'react-immutable-proptypes';
import {connect} from 'react-redux';

import {Station} from "../models/station";
import {stationSelected} from "../redux/actions/selected_station_actions.js";
import {selectedStationSelector} from "../redux/selectors/selected_stations_selector";


class StationRow extends Component {
  constructor(props){
    super(props);
    this._onMouseEnter = this._onMouseEnter.bind(this);
    this._onMouseLeave = this._onMouseLeave.bind(this);
    this._onStationSelected = this._onStationSelected.bind(this);

    this.state = {hovered: false};
  }

  _onMouseEnter(e) {
    e.preventDefault();
    this.setState({hovered: true});
  }

  _onMouseLeave(e) {
    e.preventDefault();
    this.setState({hovered: false});
  }

  _onStationSelected(e){
    e.preventDefault();

    const {onStationSelected, station} = this.props;
    onStationSelected(station);
  }

  render() {
    const {station, idx, isLast, selectedStation} = this.props;
    const {hovered} = this.state;
    return (
      <div
        className={css(
          styles.stationRow,
          idx === 0 && styles.stationRowFirst,
          isLast && styles.stationRowLast,
          (hovered || selectedStation === station) && styles.stationRowHovered,
        )}
        onMouseEnter={this._onMouseEnter}
        onMouseLeave={this._onMouseLeave}
        onClick={this._onStationSelected}
      >
        <div>
        <span className={css(styles.stationField)}>
          {idx + 1}.
        </span>
          <span className={css(styles.stationField)}>
          {station.name}
        </span>
        </div>
        <div className={css(styles.stationRowRight)}>
        <span className={css(styles.stationField, styles.stationFieldTitle)}>
          Distance:
        </span>
          <span className={css(styles.stationField, styles.stationFieldRight)}>
          {station.distance}ft
        </span>
          <span className={css(styles.stationField, styles.stationFieldTitle)}>
          Time Until Ride:
        </span>
          <span className={css(styles.stationField, styles.stationFieldRight)}>
          {Math.round(station.timeUntilRide * 100) / 100} min
        </span>
          <span className={css(styles.stationField, styles.stationFieldTitle)}>
          Bikes:
        </span>
          <span className={css(styles.stationField, styles.stationFieldRight)}>
          {station.bikesAvailable}
        </span>
          <span className={css(styles.stationField, styles.stationFieldTitle)}>
          Total:
        </span>
          <span className={css(styles.stationField)}>
          {station.totalSpots}
        </span>
        </div>
      </div>
    );
  }
}

Station.propTypes = {
  station: P.instanceOf(Station).isRequired,
  idx: P.number.isRequired,
  isLast: P.bool.isRequired,
  onStationSelected: P.func.isRequired,
  selectedStation: P.instanceOf(Station),
}


function StationList({stationData, onStationSelected, selectedStation}) {
  return (
    <div
      className={css(styles.wrapper)}
    >
      {
        stationData.map((station, idx) => (
          <div
            key={idx}
          >
            <StationRow
              idx={idx}
              station={station}
              selectedStation={selectedStation}
              onStationSelected={onStationSelected}
              isLast={idx === (stationData.size - 1)}
            />
          </div>
        ))
      }
    </div>
  );
}

StationList.propTypes = {
  stationData: IP.listOf(P.instanceOf(Station)).isRequired,
  onStationSelected: P.func.isRequired,
  selectedStation: P.instanceOf(Station),
}

StationList.defaultProps = {
  selectedStation: null,
};

const mapDispatchToProps = (dispatch) => ({
  onStationSelected: (selectedStation) => {
    dispatch(stationSelected(selectedStation));
  }
});
const mapStateToProps = (state) => {
  return {
    selectedStation: selectedStationSelector(state),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(StationList);

const styles = StyleSheet.create({
  stationField: {
    fontFamily: '"Helvetica Neue", Helvetica, Arial, sans-serif',
    fontSize: 15,
    marginLeft: 5,
    marginRight: 5,
  },
  stationFieldRight: {
    marginRight: 15,
  },
  stationFieldTitle: {
    fontWeight: 'bold',
    fontSize: 14,
    marginRight: 2,
  },
  stationRowRight: {
    display: 'flex',
    flexFlow: 'row nowrap',
    justifyContent: 'flex-end',
  },
  wrapper: {
    position: 'relative',
    display: 'flex',
    flexFlow: 'column nowrap',
    borderRadius: 10,
    width: '100%',
    height: 500,
    minHeight: 300,
    backgroundColor: '#0098e4',
    marginTop: 20,
    padding: 20,
    overflowY: 'auto',
  },
  stationRow: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    height: 35,
    padding: 5,
    marginTop: 10,
    marginBottom: 10,
    backgroundColor: 'white',
    borderRadius: 5,
  },
  stationRowHovered: {
    backgroundColor: 'lightgray',
    cursor: 'pointer',
  },
  stationRowFirst: {
    marginTop: 0,
  },
  stationRowLast: {
    marginBottom: 0,
  },

});


