import {List} from 'immutable';
import {makeStationFromRaw} from "../../models/station.js";
import {fetchingStationsDataSelector} from "../selectors/stations_selector";
import StationApi from "../../api/station_api.js";

const FETCH_STATIONS = 'FETCH_STATIONS';
const RECEIVED_STATIONS_SUCCESS = 'RECEIVED_STATIONS_SUCCESS';
const RECEIVED_STATIONS_FAILURE = 'RECEIVED_STATIONS_FAILURE';

export const actionTypes = {
  FETCH_STATIONS,
  RECEIVED_STATIONS_SUCCESS,
  RECEIVED_STATIONS_FAILURE,
};

export function fetchStations(isFetching) {
  return {
    type: FETCH_STATIONS,
    payload: {
      isFetching,
    },
  };
}

export function receivedStationsSuccess(rawStations) {
  const stationList = rawStations ? List(rawStations.map(s => (makeStationFromRaw(s)))) : List();

  return {
    type: RECEIVED_STATIONS_SUCCESS,
    payload: {
      stationData: stationList,
    },
  };
}

export function receivedStationsFailure(error) {
  return {
    type: RECEIVED_STATIONS_FAILURE,
    payload: {
      error,
    },
  };
}

export function fetchStationsForLocation(latitude, longitude, isLookingForBike) {
  return (dispatch, getState) => {
    if (!fetchingStationsDataSelector(getState())) {
      if (!latitude || !longitude) {
        dispatch(receivedStationsFailure("Need location to query"));
      }

      dispatch(fetchStations(true));
      return StationApi.getStationByCoordinates(latitude, longitude, isLookingForBike).then(
        (rawStations) => dispatch(receivedStationsSuccess(rawStations)),
        (error) => dispatch(receivedStationsFailure(error))
      );
    }
  };
}


